.drawer-menu {
  display: none;
}
@media screen and (max-width: 767px) {
  .drawer-menu {
    display: block;
    border-top: 0.25rem solid #488ad9;
    overflow: scroll;
    background: #fff;
    width: 70%;
    position: fixed;
    top: 4.8125rem;
    right: 0;
    z-index: 400;
    transform: translateY(-200%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    /* 高さを画面全体に設定 */
    max-height: calc(100vh - 4.8125rem); /* 最大高さ: ビューポート高さ - ヘッダーの高さ */
    height: auto; /* 内容に応じて高さを調整 */
  }
}

.drawer-menu.is-clicked {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 767px) {
  .drawer-menu__wrap {
    /* ラッパー要素がスクロール可能に */
    height: 100%; /* ドロワーメニュー内で高さ全体を使用 */
    overflow-y: auto; /* 縦スクロールを有効化 */
  }
}

/* 背景オーバーレイ */
.js-drawer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; /* 横幅全体 */
  height: 100%; /* 高さ全体 */
  background-color: rgba(163, 169, 181, 0.5); /* 背景色: 半透明 */
  z-index: 399; /* ドロワーメニューの下に配置 */
  display: none; /* 初期状態では非表示 */
}

/* オーバーレイ表示時 */
.js-drawer-overlay.is-active {
  display: block;
}

